"use client"

import { useState } from "react"
import { BookCard } from "@/components/BookCard"
import { SearchBar } from "@/components/SearchBar"
import { getBooks, searchBooks } from "@/lib/books"
import { Alert, AlertDescription } from "@/components/ui/alert"

export default function CatalogPage() {
  const [searchQuery, setSearchQuery] = useState("")
  const allBooks = getBooks()
  const books = searchQuery ? searchBooks(searchQuery) : allBooks

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4">Library Catalog</h1>
          <SearchBar
            value={searchQuery}
            onChange={setSearchQuery}
            onClear={() => setSearchQuery("")}
          />
        </div>

        {/* Books Grid */}
        {books.length > 0 ? (
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-6">
            {books.map((book) => (
              <BookCard key={book.id} book={book} />
            ))}
          </div>
        ) : (
          <Alert>
            <AlertDescription>
              No books found matching your search criteria. Try adjusting your search terms.
            </AlertDescription>
          </Alert>
        )}
      </div>
    </div>
  )
}
